.non-resume h1 {
  display: none;
}
#landing {
  text-align: center;
}
#landing .avatar img {
  border: 5px solid #ffffff;
  border-radius: 50%;
  margin-top: 75px;
  width: 25%;
  -webkit-box-shadow: 0px 6px 13px 0px #dbdbdb;
  box-shadow: 0px 6px 13px 0px #dbdbdb;
}
@media only screen and (max-width: 600px) {
  #landing .avatar img {
    width: 75%;
  }
}
#location p {
  text-align: center;
}
#location p:first-of-type {
  margin-top: 50px;
}
#location .map {
  -webkit-box-shadow: 0px 6px 13px 0px #dbdbdb;
  box-shadow: 0px 6px 13px 0px #dbdbdb;
  height: 400px;
  margin-left: 12.5%;
  width: 75%;
}
h2 {
  color: #686868;
  font-size: 4em;
  font-weight: 400;
}
h3 {
  color: #686868;
  font-size: 3em;
  font-weight: 400;
}
.Section {
  color: #686868;
  font-size: 1.2em;
  margin: 20px 0px;
}
.dark-text {
  color: #686868;
}
.slight-shadow {
  -webkit-box-shadow: 0px 6px 13px 0px #dbdbdb;
  box-shadow: 0px 6px 13px 0px #dbdbdb;
}
body {
  margin: 0px 20px;
  padding: 0;
  font-family: Lato, Helvetica, sans-serif;
}


